import { EdgioError } from './EdgioError'
import { limitsLink, serverLogsLink } from './edgioErrorLinks'

/*
  This error is triggered when default Node's value (80KB) for the maximum size of the response headers is exceeded.
  See https://github.com/nodejs/http-parser/blob/678a9e21f11e5ea93e5e17a2302390f88a6a8c46/http_parser.h#L64
 */
export class EdgioHeadersOverflowError extends EdgioError {
  constructor(details: string = 'HPE_HEADER_OVERFLOW', stack?: string) {
    super({
      statusCode: 542,
      title: 'Edgio Headers Overflow',
      message: `The project's cloud function returned HTTP response headers with an overall size greater than 80KB or single header with value greater than 32KB. If you are the website administrator, please use ${serverLogsLink} to debug and decrease the response headers size. See: ${limitsLink}`,
      level: 1,
      details,
      stack,
    })
  }
}
