import { EdgioError } from './EdgioError'
import { supportLink } from './edgioErrorLinks'

export class EdgioInternalError extends EdgioError {
  constructor(details?: string, stack?: string, code?: string) {
    super({
      statusCode: 530,
      title: 'Edgio Internal Error',
      message: `Unexpected error in one of Edgio's cloud function components. If you are the website administrator, please contact the ${supportLink} immediately.`,
      level: 1,
      details,
      stack,
    })
  }
}
